.preview-list-container {
    width: 900px;
    margin: 0 auto;
    display: flex;
    flex-direction: row-reverse;
    justify-content: space-between;
    flex-wrap: wrap;

    @include respond(tab-land) {
        width: 80%;
    }

    @include respond(small-phone) {
        width: 90%;
    }

    .form-divider {
        margin-bottom: 0;
    }
}

.preview-list__empty-message {
    height: 100%;
    display: grid;
    place-items: center;
    font-size: 1.7rem;
    color: lighten($color: $color-text-grey, $amount: 35);
}

.preview-list {
    width: 48%;
    margin-top: 3rem;
    @include boxshadow;
    
    @include respond(phone) {
        width: 100%;
    }

    &-table {
        height: 200px;
        overflow-y: scroll;
        width: 100%;
        background-color: $color-white;

        @include respond(phone) {
            height: 150px;
        }
        
        &__row-item {
            height: 50px;
            padding: 0 0 0 1rem;
            display: flex;
            flex-direction: row-reverse;
            justify-content: space-between;
            align-items: center;
            background-color: white;
            transition: all .2s;
            border-bottom: 2px solid rgba(black, .05);

            &:hover {
                background-color: $color-bg-grey;
            }
        }

        &__username {
            color: $color-primary;
            height: 100%;
            display: flex;
            flex-direction: column;
            align-items: flex-end;
            justify-content: center;
            padding-right: 1rem;
        }

        &__role {
            color: darkgray;
            font-size: 1.2rem;
            overflow: hidden;
            text-overflow: ellipsis;
            display: -webkit-box;
            -webkit-line-clamp: 1; /* number of lines to show */
            -webkit-box-orient: vertical;
        }
        
        &__more-container {
            display: grid;
            place-items: center;
        }



        &__more-btn {
            background-color: $color-primary;
            color: $color-white;
            padding: .3rem .7rem;
            border-radius: 3px;
            font-size: 1.3rem;
            box-shadow: 0 .2rem .1rem rgba(black, .1);
            cursor: pointer;
            transition: all .2s;
            backface-visibility: hidden;
            text-decoration: none;

            &:hover {
                box-shadow: 0 .3rem .2rem rgba(black, .1);
                transform: translateY(-2px);
            }

            &:focus,
            &:active {
                box-shadow: 0 .2rem .1rem rgba(black, .1);
                transform: translateY(-1px);
            }
        }

        &__confirm-container {
            display: flex;
            flex-direction: row-reverse;
        }

        &__accept-btn, &__reject-btn {
            padding: .3rem .7rem;
            border-radius: 3px;
            font-size: 1.3rem;
            box-shadow: 0 .2rem .1rem rgba(black, .1);
            cursor: pointer;
            transition: all .2s;
            backface-visibility: hidden;
            text-decoration: none;

            &:hover {
                box-shadow: 0 .3rem .2rem rgba(black, .1);
                transform: translateY(-2px);
            }

            &:focus,
            &:active {
                box-shadow: 0 .2rem .1rem rgba(black, .1);
                transform: translateY(-1px);
            }
        }

        &__accept-btn {
            color: $color-white;
            background-color: $color-confirm;
        }

        &__reject-btn {
            color: $color-white;
            background-color: $color-danger;
            margin-right: 1rem;
        }
    }
}

.preview-list-full {
    width: 100%;
    margin-bottom: 3rem;
    @include boxshadow;
}

.preview-list-filter {
    width: 90%;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 3rem;
    @include boxshadow;

    @include respond(tab-port) {
        width: 100%;
    }
}


.fixed-big-height {
    height: 400px;
}