
.alert {
    position: relative;

    &__info {
        color: $color-text-grey;
        background-color: lighten($color-text-grey, 55);
    }

    &__error, &__400, &__401, &__402, &__403, &__404, &__405, &__500{
        color: $color-danger;
        background-color: lighten($color-danger, 45);
    }
    
    &__warning, &__300 {
        color: $color-warning;
        background-color: #FFF3CD;
    }
    
    &__success, &__200, &__201 {
        color: $color-success;
        background-color: #d4edda;
    }

    &__info,
    &__error, 
    &__400, 
    &__401, 
    &__402, 
    &__403, 
    &__404,
    &__405, 
    &__500,
    &__warning, 
    &__300,
    &__success, 
    &__200, 
    &__201 {
        font-size: 1.2rem;
        padding: 0rem 1.8rem 0rem 1rem;
        margin: 1.5rem .8rem;
        border-radius: 3px;
        
        @include respond(tab-port) {
            margin: 0rem .8rem 1rem .8rem;
        }
        &-message {
            margin: .4rem 0;

            &:first-child {
                padding-top: 1rem;
            }

            &:last-child {
                padding-bottom: 1rem;
            }

        }
    }
}

.form--login, .form--general {
    .alert {
        &__info,
        &__error, 
        &__400, 
        &__401, 
        &__402, 
        &__403, 
        &__404,
        &__405, 
        &__500,
        &__warning, 
        &__300,
        &__success, 
        &__200, 
        &__201 {
            margin: 1.5rem 0rem;
        }
    }
}

.display-item-btn {
    position: absolute;
    left: 0;
    top: 50%;
    transform: translateY(-50%);
    background-color: $color-primary;
    padding: .4rem 1rem;
    color: $color-white;
    text-decoration: none;
    border-radius: 3px;
    margin-left: 1.5rem;
}