.content-divider {
    text-align: center;
    margin: 3rem 0 3rem 0;
    position: relative;
    width: 100%;

    &__message {
        font-size: 1.2rem;
        position: relative;
        display: inline-block;
        z-index: 9;
        background-color: $color-white;
        padding: 0 1rem
    }

    &::before {
        display: block;
        content: " ";
        width: 100%;
        height: 1px;
        background-color: #ddd;
        position: absolute;
        top: 50%;
        left: 0;
        transform: translateY(-50%);
    }
}