/* PRINTING STYLES */

@page {
    background-color: red;
}

@media print {

    .qr-sticker-constainer-chrome, .qr-sticker-constainer-not-chrome {

        input[type="text"]:disabled {
            background-color: $color-white;
        }

        label, input {
            font-size: 1.6rem;
        }

        .form-group__1-col, .form-group__1-col-datepicker {
            display: flex;
            flex-direction: row-reverse;
            justify-content: center;
            align-items: center;
            flex-wrap: nowrap;
        }

        .form-group {
            margin-bottom: 1rem;
        }

        label {
            margin-bottom: .5rem;
            width: 90px;
        }

        input {
            border: 1px solid rgba($color-primary, .6);
            padding-right: 1.5rem;
            height: 2.8rem;
            margin-bottom: 1rem;
            width: 420px !important;
        }

        .form__input--icon {
            display: none;
        }

        .display-on-print {
            display: block;
        }

        .no-display-on-print {
            display: none;
        }

        .qr-sticker {
            width: 120mm;
            display: flex;
            justify-content: center;
            align-items: flex-start;
            margin-left: 5mm;
            canvas {
                height: 40mm !important;
                width: 40mm !important;
            }
        }

        .form-group-qr {
            width: 150mm;
            margin-top: 5mm;
            margin-right: 10mm;
        }
    }
        .pagebreak {
            page-break-before: always;
          }
}


@media print {
    .qr-sticker-constainer-not-chrome {
        border: 2px solid $color-primary;
        padding: 5mm 0;
        position: relative;
        top: 0mm;
        right: 0mm;
        transform: scale(1);
        padding-right: 12mm;
        margin-right: 0mm;
        overflow: hidden;
    }

    .qr-sticker-constainer-chrome {
        border: 2px solid $color-primary;
        padding: 5mm 0;
        position: absolute;
        top: 35mm;
        right: 70mm;
        width: 210mm;
        height: 55mm;
        transform: scale(1.5);
        padding-right: 12mm;
        margin-right: 10mm;
        overflow: hidden;
    }


}