.extra-info {
    margin-top: 2rem;
    border-radius: 3px;

    & > * {
        margin-bottom: .6rem;
    }

    &__highlight-error {
        color: $color-danger;
    }

    &__highlight-warning {
        color: darken(#f1c40f, 7);
    }

    i {
        margin-left: .5rem;
        font-size: 1.2rem;
    }
} 