.form-container {
    padding: 3rem;
    padding-bottom: 5rem;
    background-color: $color-white;
    border-radius: 2rem;
    box-shadow: -.6rem .6rem 0 $color-primary;

    @include respond(phone) {
        box-shadow: 0 6px 2rem rgba(black, .1);
        width: 92%;
        padding: 2rem;
        padding-bottom: 5rem;
        border-radius: 1rem;
    }

    &__logo {
        text-align: center;
        margin-bottom: 3.5rem;

        &--medium {
            width: 6rem;
        }
    }
}

.form {

    &--login {
        width: 400px;

        @include respond(phone) {
            width: 100%;
        }

        @include respond(small-phone) {
            width: 100%;
        }
    }

    &--register {
        width: 500px;

        @include respond(phone) {
            width: 100%;
        }
    }

    &__submit {
        display: block;
        background-color: $color-primary;
        border: 2px solid $color-primary;
        padding: .7rem 3rem;
        font-size: 1.3rem;
        color: $color-white;
        border-radius: 3px;
        width: 100%;
        font-family: tajwaal;
        transition: all .2s;
        cursor: pointer;

        &:hover {
            transform: translateY(-3px);
            box-shadow: 0 .5rem 1rem rgba(black, .2);
        }

        &:focus,
        &:active {
            outline: none;
            transform: translateY(-1px) ;
            box-shadow: 0 .3rem .5rem rgba(black, .1);
        }
    }

    &__input {
        height: 3.8rem;
        border: 1px solid rgba(#555, .1);
        border-radius: 3px;
        text-align: right;
        margin-bottom: 1.4rem;
        padding-right: 4rem;
        color: $color-text-grey;
        font-family: tajwaal;

        &:focus {
            outline: none;
            border: 1px solid rgba($color-primary, .7);
        }

        &:placeholder-shown {
            font-family: tajwaal;
            font-size: 1.3rem;
            color: $color-input-placeholder;
        }

        &::selection{
            background-color: #C8C8C8;
        }
        
        &::-moz-selection {
            background-color: #C8C8C8;
        }

        &[type="text"]:disabled {
            background-color: #FAFAFA;
        }

        &--icon {
            color: $color-input-placeholder;
            position: absolute;
            top: 1.1rem;
            right: 1.1rem;
            font-size: 1.6rem;
        }



    }

    &__text-area {
        height: 18rem;
        border: 1px solid rgba(#555, .1);
        border-radius: 3px;
        text-align: right;
        padding-right: 1rem;
        padding-top: 1rem;
        color: $color-text-grey;
        font-family: tajwaal;
        resize: none; 

        &:focus {
            outline: none;
            border: 1px solid rgba($color-primary, .7);
        }

        &:placeholder-shown {
            font-family: tajwaal;
            font-size: 1.3rem;
            color: $color-input-placeholder;
        }

        &--min-limit {
            margin-top: .2rem;
            font-size: 1.4rem;
        }

        &--full-limit {
            margin-top: .2rem;
            font-size: 1.4rem;
            color: $color-danger;
        }

    }

    &__label {
        margin-bottom: .7rem;
    }

    &__input,
    &__label,
    &__text-area {
        display: block;
        width: 100%;
        font-size: 1.4rem;
    }


}

.form-group {
    width: 100%;
    display: flex;
    justify-content: space-between;
    flex-direction: row-reverse;

    @include respond(small-phone) {
        flex-direction: column;
    }

    &-btn {
        width: 50%;
        display: flex;
        justify-content: space-between;
        flex-direction: row-reverse;
        align-items: center;
        @include respond(small-phone) {
            width: 100%;
        }

        button {
            width: 25px;
            height: 25px;
            margin-right: 1rem;
        }
    }

    &-dynamic {
        width: 100%;
        display: flex;
        justify-content: space-between;
        flex-direction: row-reverse;
        align-items: center;
    }


    &--fixed {
        width: 100%;
        display: flex;
        justify-content: space-between;
        flex-direction: row-reverse;
    }

    &__dynamic-col {
        position: relative;
        width: 95%;

        &--btn {
            display: flex;
            justify-content: flex-end;
            margin-right: 1rem;
            width: 100%;
            @include respond(phone) {
                width: 3rem;
            }
        }
    }


    &__1-col {
        position: relative;
        width: 100%;

        &--center {
            text-align: center;
        }
    }

    &__2-col {
        position: relative;
        width: 48%;
        
        @include respond(small-phone) {
            width: 100%;
        }


        &--fixed {
            width: 50%;
        }
    }

    &__3-col {
        position: relative;
        width: 31%;
        
        @include respond(small-phone) {
            width: 100%;
        }

    }

}

.form-control {
    position: relative;
}

.forget-pass {
    display: inline-block;
    margin-top: .8rem;
    margin-bottom: 2rem;
    text-align: center;
    font-size: 1.2rem;
    text-decoration: none;
    border-bottom: 1px solid $color-primary;
    &,
    &:link,
    &:active {
        color: $color-primary;
    }
}

.reseted-info {
    font-size: 1.8rem;
}

.form-error {
    background-color: lighten($color-danger, 45);
    color: $color-danger;
    padding: 2rem 2rem;
    margin-bottom: 2rem;
    border-radius: 3px;

    &__message {
        font-size: 1.2rem;
        
        &:not(:last-child) {
            margin-bottom: .4rem;
        }
    }
}

