.flex-row {
    width: 100%;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;

    &__1-col {
        width: 100%;
    }

    &__2-col {
        width: 45%;

        &--align-left {
            text-align: left;
        }
    }

    &__3-col {
        width: 30%;
    }

}