.dashboard {
    margin-top: 2rem;
    font-family: 'droid';
    overflow: hidden;
    &-overview-title {
        font-size: 1.7rem;
        margin-bottom: .5rem;
        text-align: right;
        width: 100%;
        margin-top: 1rem;
    }
}

.dashboard-flex {
    display: flex;
    flex-direction: row-reverse;
    justify-content: space-between;
    flex-wrap: wrap;
    
    &__60 {
        width: 59%;

        @include respond(tab-land) {
            width: 100%;
        }

    }

    &__50 {
        width: 49%;
        
        @include respond(tab-land) {
            width: 100%;
        }
    }

    &__40 {
        width: 39%;

        @include respond(tab-land) {
            width: 100%;
        }
    }
}

.dashboard-filter {
    display: flex;
    flex-direction: row-reverse;
    align-items: baseline;
    width: 100%;
    flex-wrap: wrap;

    .filter__fields {
        display: flex;
        flex-direction: row-reverse;
        align-items: baseline;    
        width: 50%;

        @include respond(phone) {
            width: 100%;
            justify-content: center;
        }

        @include respond(small-phone) {
            flex-wrap: wrap;
        }

        .form-group__dashboard-col-datepicker {
            width: 200px;

            @include respond(small-phone) {
                width: 100%;
            }
        }
        
        & > i {
            margin: 0 1rem;
            font-size: 1.2rem;

            @include respond(small-phone) {
                display: none;
            }
        }
    }

    .confirm-group {
        width: 50%;
        flex-direction: row-reverse;
        justify-content: flex-end;

        & > i {
            font-size: 1.2rem;
            visibility: hidden;

            @include respond(phone) {
                margin: 0 1rem;
            }   
        }

        input {
            margin: 0;
        }

        @include respond(phone) {
            width: 100%;
            justify-content: center;

            input {
                width: 200px;
            }
        }

        @include respond(small-phone) {
            flex-wrap: wrap;

            & > i {
                display: none;

            }
            input {
                width: 100%;
                margin-bottom: 1rem;
            }
        }
    }
}


.dashboard-metrics {
    display: flex;
    flex-direction: row-reverse;
    width: 100%;
    background-color: $color-white;
    text-align: center;
    padding: 2rem 0rem;
    margin-bottom: 1.5rem;
    border-radius: 2px;
    justify-content: center;
    @include boxshadow;

    &__metric {
        height: 10rem;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        width: 33.333333%;
        &:not(:last-child) {
            border-left: 2px solid #efefef;
        }

        &--label {
            font-family: 'droid';
            font-size: 1.3rem;
        }

        &--value {
            font-weight: 700;
            font-size: 3.5rem;
            font-family: tajwaal;
        }


    }
}

.dashboard-list {
    background-color: $color-white;
    @include boxshadow;
    height: 100%;

    &__header {
        display: flex;
        justify-content: space-between;
        background-color: #efefef;
        flex-direction: row-reverse;

        &--title {
            width: 50%;
            text-align: center;
            font-size: 1.6rem;
            padding: .5rem 0;
        }
    }

    &__body {
        overflow-y: scroll;
        height: 15rem;
    }

    &__row {
        display: flex;
        flex-direction: row-reverse;
        border-bottom: 1px solid #efefef;

        &--value {
            
            padding: .5rem 0;
            text-align: center;
            width: 50%;

            &:first-child {
                border-left: 1px solid #efefef;
            }
        }
    }
}


.dashboard-chart {
    background-color: $color-white;
    display: flex;
    justify-content: center;
    align-items: center;
    @include boxshadow;

    &-calender {
        
        padding: 2rem 0;
        overflow-y: scroll;
        align-items: center;
        @include boxshadow;
        
        @media screen and (max-width: 1280px) {
            justify-content: flex-start;
        }
     }

     &-area {
        overflow-x: scroll;

        @media screen and (max-width: 1490px) {
            justify-content: flex-start;
        }

        &-row-span-list {
            height: 49.5rem;
        }
     }

     &-pie {
         overflow-x: scroll;

         @include respond(fold-phone) {
            justify-content: flex-start;
        }
     }

     &-loading {
         height: 20rem;
         display: grid;
         place-items: center;
     }
}


.dashboard-card-container {
    display: flex;
    flex-direction: row-reverse;
    justify-content: space-between;
    width: 100%;

    .dashboard-card {
        width: 49.5%;
        

        &__content {
            padding: 2rem 0rem;
            background-color: $color-white;
            @include boxshadow;

            &--value {                
                display: grid;
                place-items: center;
                height: 10rem;
                font-size: 5.5rem;
                font-family: tajwaal;
                background-color: $color-white;
            }
        }
    }
}

.rate-separator {
    font-size: 3rem;
}
