.radio-question {

    &__question-text {
        margin-top: 1rem;
        margin-bottom: .5rem;
    }


    &:not(:first-of-type) {
        margin-top: 3rem;
    }



    &__options-container {
        display: flex;
        justify-content: flex-start;
        flex-direction: row-reverse;
        flex-wrap: wrap;
    }

    &__radio-option {
        width: 25%;
        display: flex;
        justify-content: flex-end;
        margin-top: 1rem;
        @include respond(tab-port) {
            width: 35%;
        }

        @include respond(phone) {
            width: 55%;
        }

    }

    &__radio-label {
        cursor: pointer;
        display: flex;
        flex-direction: row-reverse;
        align-items: center;
        width: 100%;
        padding-right: 5px;
    }

    &__radio-label-text {
        width: 80%;
        padding-right: 2px;
    }

    &__radio-btn {
        display: none;
    }

    &__custom-radio-btn {
        position: relative;
        height: 2.4rem;
        width: 2.4rem;
        border: 4px solid $color-primary;
        border-radius: 50%;
        display: inline-block;
        display: grid;
        place-content: center;
        margin-left: .5rem;
        backface-visibility: hidden;

        @include respond(tab-port) {
            border-width: 3.5px;
        }

        &::after {
            height: 1rem;
            width: 1rem;
            content: "";
            display: block;
            border-radius: 50%;
            background-color: $color-primary;
            opacity: 0;
            position: absolute;
            left: 50%;
            top: 50%;
            transform: translate(-50%, -50%);
            transition: opacity .2s;
            backface-visibility: hidden;

        }
    }

    &__radio-btn:checked ~ &__radio-label &__custom-radio-btn::after {
        opacity: 1;
    }
}

