.right-side {
    background-color: $color-white;
    position: fixed;
    right: 0;
    top: 0;
    height: 100%;
    width: 300px;
    z-index: 2000;
    transition: all .4s;
    box-shadow: 100rem 0 100rem rgba(black, .2);
    
    /*
    @include respond(tab-port) {
        margin-right: -300px;
        margin-top: 56px;
    }
    */

    &.close {
        @include respond(tab-port) {
            margin-right: -300px;
            margin-top: 56px;

            // THIS IS FOR DYNAMIC NAV LIST
            .right-side__nav-list {
                right: -300px;
                transition: right .4s;
            }

        }
    }

    &.open {
        
        @include respond(tab-port) {
            margin-right: 0px;
            margin-top: 56px;
            box-shadow: -30rem 0 5rem 50rem rgba(black, .3);


        }

        @include respond(small-phone) {
            width: 100%;    
            box-shadow: -5rem 0 10rem rgba(black, .4);
        }



        
    }

    &__logo {
        display: grid;
        place-items: center;
        padding: 2rem 0;

        &--logo {
            height: 3.2rem;

            @include respond(big-desktop) {
                // if font-size in big-desktop return to 75%, REMOVE this
                height: 3.3rem;
            }
        }
    }

    &__user-profile {
        display: flex;
        flex-direction: row-reverse;
        align-items: center;
    }

    &__profile {
        display: flex;
        flex-direction: row-reverse;
        justify-content: space-between;
        align-items: center;
        padding: 2rem 2rem;
        border-top: 1px solid #E6E6E6;
        border-bottom: 1px solid #E6E6E6;

        &-avatar {
            font-size: 1.4rem;
            display: grid;
            place-items: center;
            color: $color-primary;
            background-color: rgba(#CBCBCB, .4);
            width: 4.2rem;
            height: 4.2rem;
            border-radius: 8px;

            @include respond(tab-port) {
                font-size: 2rem;
                width: 5.5rem;
                height: 5.5rem;
            }
        }

        &-identity {
            margin-right: 1.2rem;
            &--name {
                color: $color-primary;
                font-size: 1.5rem;
                word-spacing: .1rem;

                @include respond(tab-port) {
                    font-size: 1.8rem;
                }
            }

            &--role {
                font-size: 1.1rem;
                color: #979798;
                @include respond(tab-port) {
                    font-size: 1.3rem;
                }
            }
        }

        &-anchor {
            font-size: 1.9rem;
            font-weight: 100;
            color: #979798;

            @include respond(tab-port) {
                font-size: 2.8rem;
            }
        }
    }

    &__nav {
        position: relative;

        &-list {
            padding-top: 1.5rem;
            list-style: none;
            font-size: 1.4rem;


            // IF YOU WANT DYNAMIC NAV LIST, REMOVE THIS PROPERTY AND UN COMMENT BELOW
            // height: 100%;


            // IN CASE WE HAVE A LOT OF NAVIGATION ITEMS AND IT NEED TO BE OVERFLOW AND DYNAMIC

            
            
            overflow-y: scroll;
            position: fixed;    
            top: 16.7rem;
            bottom: 0;
            width: 300px;
            right: 0;



            @include respond(tab-port) {
                top: 23.7rem;
            }

            

            &--header {
                display: flex;
                flex-direction: row-reverse;
                align-items: center;
                border-right: 7px solid $color-primary;
                color: $color-primary;
                padding-right: 2rem;
                height: 4rem;
                font-size: 1.5rem;
                @include respond(tab-port) {
                    height: 4.5rem;
                    font-size: 1.8rem;
                }
            }

            &--item {
                display: flex;
                flex-direction: row-reverse;
                align-items: center;
                height: 4rem;
                padding-right: 3rem;
                font-size: 1.4rem;
                cursor: pointer;
                transition: all .15s;
                
                @include respond(tab-port) {
                    height: 5rem;
                    font-size: 1.7rem;
                }

                &:hover {
                    background-color: #EFEFEF;
                }

                &,
                &:visited,
                &:link {
                    text-decoration: none;
                    color: $color-text-grey;
                }
            }
        }
    }
}


.left-side {
    background-color: $color-bg-grey;
    color: $color-white;
    //margin-right: 300px;
    min-height: 100vh;
    padding-bottom: 2rem;
    position: relative;
    
    @include respond(tab-port) {
        margin-right: 0px;
    }

    &__breadcrumbs {
        position: sticky;
        top: 0;
        color: $color-text-grey;
        background-color: $color-white;
        padding: .5rem 4rem;
        z-index: 1000;
        @include boxshadow;


        .separator {
            font-size: 2rem;
            margin: 0 1rem;
            color: $color-primary;

            &:last-of-type {
                display: none;
            }
        }

        @include respond(tab-port) {
            background-color: transparent;
            box-shadow: none;
            position: static;

            .secondary-header {
                // Removing the BIG breadcrumbs header and display only the small
                display: none;
            }
        }

        &-items {
            display: flex;
            justify-content: flex-start;
            align-items: center;
            flex-wrap: wrap;
            flex-direction: row-reverse;
            i {
                margin-left: .3rem;
                color: $color-primary;
            }

            &--item {
                display: inline-block;
                margin-top: 3px;
                font-size: 1.2rem;
                color: $color-primary;
                
                &:last-of-type {
                    color: $color-text-grey;
                    text-decoration: none;
                    cursor: default;
                }

                @include respond(tab-port) {
                    margin: 1.2rem 0;
                    font-size: 1.6rem;
                }
            }
        }
    }

}

.content {
    color: $color-text-grey;

    &__item {
        height: 10rem;
    }
}

