.resource-not-found {

    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    overflow: hidden;
    text-align: center;
    &__code {
        font-size: 25rem;
        font-family: kdam;
        color: lighten($color: $color-danger, $amount: 40);
        color: lightgray;

        @include respond(small-phone) {
            font-size: 20rem;
        }
    }

    &__message {
        margin-top: -11rem;
        font-size: 2.5rem;

        @include respond(small-phone) {
            font-size: 2rem;
        }
    }

    &__referer {
        color: $color-white;
        background-color: $color-primary;
        text-align: center;
        &,
        &:visited {
            color: white;
            margin: 0 1rem;
            margin-top: 8rem;
        }

        @include respond(phone) {
            margin-top: 5rem;
        }

        @include respond(small-phone) {
            width: 90%;
        }
    }
}

.resource-not-found-full {
    height: 100vh;
    background-color: $color-bg-grey;
    display: flex;
    justify-content: flex-start;
    padding-top: 10rem;

}