@font-face {
    font-family: kurdish;
    src: url('../../fonts/Ubuntu-Kurdish-Kurdfont.ttf');
}


@font-face {
    font-family: tajwaal;
    src: url('../../fonts/Tajawal-Medium-2.ttf');
}


@font-face {
    font-family: kdam;
    src: url('../../fonts/KdamThmor-Regular.ttf');
}

@font-face {
    font-family: droid;
    src: url('../../fonts/ArbFONTS-Droid\ Arabic\ Kufi.ttf');
    font-weight: 400;
}


@font-face {
    font-family: droid;
    src: url('../../fonts/ArbFONTS-Droid\ Arabic\ Kufi\ Bold.ttf');
    font-weight: 900;
}



html {
    font-size: 62.5%;

    @include respond(big-desktop) {
        // previous was 75%
        font-size: 68%; // 1rem = 12px
    }

    @include respond(tab-land) { // width < 900 
        font-size: 56.25%; // 1rem = 9px 
    }

    @include respond(tab-port) { // width < 600
        font-size: 50%; // 1rem = 8px, 8/16 = 50%
    }
}


body {
    color: $color-text-grey;
    font-family: tajwaal;
    text-align: right;
}


.secondary-header {
    font-size: 2.5rem;
    font-weight: 100;
    color: $color-text-grey;
}

.trienary-header {
    font-size: 1.6rem;
    font-weight: 100;
    color: $color-text-grey;
}