.dd-wrapper {
    width: 100% !important;
}

.dd-header {
    height: 3.8rem;
    width: 100% !important;
    overflow: hidden !important;
    border-radius: 5px;
    margin-bottom: 1.4rem !important;

    @include respond(small-phone) {
        width: 100% !important;
    }
    
    &:focus,
    &:active {
        outline: none;
        border: 1px solid rgba($color-primary, .7) !important;
    }

    &-title {
        font-size: 1.3rem;
        width: 100%;
        margin-right: 1.5rem !important;
        text-align: right !important;
    }

    span {
        display: grid !important;
        place-items: center;
        margin-right: 0 !important;
        height: 100%;
        width: 5rem;
        background-color: $color-primary;
        
        i {
            color: $color-white;
            font-size: 1.8rem;
        }
    }
}

.dd-list, .dd-scroll-list, .dd-wrapper {
    //z-index: 1000 !important;
}

.dd-list {
}

.dd-list-item {
    font-weight: 100 !important;
    font-size: 1.3rem !important;

    &:hover {
        background-color: $color-primary !important;
        color: $color-white !important;
    }

    &:focus {
        outline: none;
        background-color: rgba($color-primary, .7) !important;

    }
}

