.mobile-header {

    display: none;

    @include respond(tab-port) {
        background-color: $color-white;
        box-shadow: 0 .3rem .6rem rgba(black, .16);
        display: grid;
        place-items: center;
        padding: 1.1rem 1rem;
        position: sticky;
        top: 0;
        z-index: 3000;

        
    }

    @include respond(fold-phone) {
        height: $mobile-header-height + 1.4rem;
    }


    &__logo {
        height: 4.8rem;

        @include respond(fold-phone) {
            position: absolute;
            left: 0;
            margin-left: 2rem;
        }
    }

    &__nav-toggle {
        position: absolute;
        right: 0;
        font-size: 2.8rem;
        margin-right: 2rem;
        height: 100%;
        width: 3.2rem;
        display: grid;
        place-items: center;
    }

}