.text-align-left {
    text-align: left;
}

.color-primary, .color-primary + .form__input--icon {
    color: $color-primary !important;
}

.color-warning, .color-warning + .form__input--icon {
    color: orange !important;
}

.color-danger, .color-danger + .form__input--icon{
    color: $color-danger !important;
}

.color-success, .color-success + .form__input--icon {
    color: $color-success !important;
} 

.content-padding {
    padding: 0 2rem;
}

.margin-top {
    margin-top: 1rem;
}

.margin-top-big {
    margin-top: 1.5rem;
}

.margin-top-huge {
    margin-top: 3rem;
}

.margin-bottom {
    margin-bottom: 1.4rem;
}

.margin-bottom-text-area {
    margin-bottom: 1.4rem !important;
} 

.margin-bottom-small {
    margin-bottom: 1rem;
}

.no-margin-right {
    margin-right: 0 !important;
}



.visibility-hidden {
    visibility: hidden;
}

.link-btn {
    padding: .4rem 1.5rem !important;
    color: white !important;
    text-decoration: none !important;
    cursor: pointer;
}

.table-with-warning-margin-top {
    margin-top: 1rem !important;
}

