
.confirm-dialog-shadow {
    position: fixed;
    background-color: black;
    top: 0;
    left: 0;
    height: 100vh;
    width: 100vw;
    opacity: .2;
}


.confirm-dialog-container {
    position: absolute;
    top: 30%;
    left: 50%;
    transform: translateX(-50%);
    width: 40%;
    border-radius: 3px;

    @include respond(phone) {
        width: 90%;
    }

    .general-form {
        margin-top: 0;
        width: 100%;
    }

    .submit-btn {
        border-color: $color-danger;
        background-color: $color-danger;
    }
}