.filter {
    background-color: transparent;
    width: 35rem;
    position: relative;
    
    @include respond(tab-land) {
        width: 30rem;
        background-color: #efefef;
    }

    &.close {
        // if toggles as close
        @include respond(tab-land) {
            display: none;
            position: absolute;
            right: 0px;
        }
    }

    &.open {
        // if toggled as open
        @include respond(tab-land) {
            background-color: #EFEFEF;
            position: absolute;
            display: block;
            right: 0;
            box-shadow: -.5rem .5rem 1rem rgba(black, .15);
        }
    }

    .content-padding {
        padding: 0 1rem;
        @include respond(tab-land) {
            padding: 0 1.5rem;
        }
    }


    .dd-header, .form__input {
        height: 3.2rem;
    }
    
    .form__input {
        margin-top: 1rem;
        margin-bottom: 0rem;
        padding-right: 3rem;
        &--icon {
            color: $color-input-placeholder;
            position: absolute;
            top: .9rem;
            right: 1rem;
            font-size: 1.4rem;
        }
    }

    .dd-header {
        margin-top: 1rem !important;
        margin-bottom: 0rem !important;
    }

    .form-control-datepicker {
        i {
            position: absolute;
            top: -2.4rem;
        }
    }

    &-height-container {
        // background-color: #efefef;
        padding-top: 1rem;
        padding-bottom: 1rem;
        width: 100%;

        // position: absolute;
        // right: 0;
        // top: 0;
        // min-height: calc(100vh - 7.2rem);

        @include respond(tab-land) {
            background-color: #efefef;
        }

    }

    &-header {
        display: flex;
        align-items: center;
        justify-content: flex-end;
        margin-right: .5rem;    
        margin-bottom: 2rem;    
        i {
            font-size: 1.6rem;
            margin-left: .8rem;
        }
    }

    &-form {
        margin-top: 1rem;
    }

}

.filter-responsive-btn {

    // DISPLAYED AS NONE, ON 1200PX SHOW IT
    display: none;

    @include respond(tab-land) {
        position: absolute;
        top: 0;
        right: 0;
        z-index: 1;
        font-size: 1.8rem;
        background-color: #d4d4d4;
        width: 4rem;
        height: 4rem;
        display: grid;
        place-items: center;
    }
}

.filter-error-message {
    font-size: 1.2rem;
}