.text-container {
    width: 900px;
    margin: 0 auto;
    margin-top: 3rem;
    background-color: $color-white;
    border-radius: 3px;
    @include boxshadow;
    font-family: 'droid';
    padding-bottom: 2.5rem;

    @include respond(tab-land) {
        width: 80%;
    }

    @include respond(small-phone) {
        width: 90%;
    }

    &__header {
        padding: 1rem 0;
        font-size: 2.2rem;
        border-bottom: 1px solid lightgray;
        margin-bottom: 1rem;
        color: $color-primary;

        &:not(:first-child) {
            border-top: 1px solid lightgray;
            margin-top: 1.5rem;
        }
    }

    &__paragraph-container {
        margin: 0 2rem;
    }

    &__paragraph-element {
        font-size: 1.6rem;
        line-height: 3.5rem;
    }

    &__bullet-container {
        padding-top: 1.5rem;
    }

    &__bullet-title {
        margin-bottom: .8rem;
        font-size: 1.7rem;
        margin-bottom: 1.2rem;
        margin-top: 1.5rem;
    }

    &__bullet-point {
        display: flex;
        flex-direction: row-reverse;
        align-items: center;
        margin-right: 1.5rem;

        &:not(:first-child) {
            margin-top: .5rem;
        }

        i {
            margin-left: 1rem;
            font-size: 1.1rem;
            opacity: .8;
        }
    }
}