.btn,
.btn:link,
.btn:visited {
    display: block;
    color: $color-primary;
    font-size: 1.3rem;
    padding: .7rem 3rem;
    border: 2px solid $color-primary;
    border-radius: 3px;
    cursor: pointer;
    transition: all .2s;
    text-decoration: none;
    &:hover {
        background-color: $color-primary;
        color: $color-white;
        transform: translateY(-3px);
        box-shadow: 0 .5rem .5rem rgba(black, .2s);
    }

    &:active {
        transform: translateY(-1px);
        box-shadow: 0 .3rem .4rem rgba(black, .1s);
    }
}

.confirm-group {
    justify-content: flex-start;
    flex-direction: row-reverse;
    margin-bottom: 1rem;
    position: relative;

    @include respond(small-phone) {
        flex-direction: row-reverse;
    }
    
    @include respond(fold-phone) {
        position: relative;
        flex-direction: column;
    }
}

.submit-btn, .cancel-btn, .delete-btn, .cancel-btn-red {
    font-family: tajwaal;
    color: $color-white;
    padding: .4rem 1.5rem;
    @include boxshadow;


    @include respond(fold-phone) {
        position: relative;
        width: 100%;

        &:not(:first-child) {
            margin-top: 1rem;
        }
    }

    &:active,
    &:focus {
        outline: none;
    }


}

.submit-btn {
    background-color: $color-primary;
    text-decoration: none;
    color: $color-white;
}

.cancel-btn {
    background-color: $color-confirm;
    border-color: $color-confirm !important;
    margin-right: 1rem;

    &:hover {
        background-color: $color-confirm !important;
    }
}

.cancel-btn-red {
    background-color: $color-danger;
    border-color: $color-danger !important;
    text-decoration: none;
    color: $color-white;
    font-family: Tajwaal;
    margin-right: 1rem;

    &:hover {
        background-color: $color-danger;
    }
}
 
.delete-btn {
    background-color: $color-danger;
    border-color: $color-danger;
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;

    &:hover {
        background-color: $color-danger;
    }

    @include respond(fold-phone) {
        position: relative;
    }
}

.btn-icon {
    margin-left: .5rem;
}

.delete-field-btn {
    height: 2.2rem;
    width: 2.2rem;
    font-size: 1.4rem;
    margin-top: 1.2rem;
    background-color: $color-danger;
    color: $color-white;
    border: none;
    padding: .5rem;
    font-weight: 100;
    border-radius: 3px;
    @include boxshadow;
    cursor: pointer;

    @include respond(tab-port) {
        height: 2.6rem;
        width: 2.6rem;
        font-size: 1.5rem;
    }

}