.general-form {
    width: 900px;
    margin: 0 auto;
    margin-top: 3rem;
    background-color: $color-white;
    border-radius: 3px;
    @include boxshadow;

    // TO DROPDOWN ISSUE, IT'S COMMINTED
    // overflow: hidden;

    padding-bottom: .5rem;

    @include respond(tab-land) {
        width: 80%;
    }

    @include respond(small-phone) {
        width: 90%;
    }


    .content-divider {
        margin-top: 1.5rem;
        margin-bottom: 1.5rem;

    }
}

.form-divider {
    
    display: flex;
    justify-content: space-between;
    flex-direction: row-reverse;
    background-color: $color-primary;
    color: $color-white;
    // background-color: $color-white;
    // color: $color-primary;
    // border-bottom: 3px solid $color-primary;
    padding: 1rem 2rem;
    font-size: 1.7rem;
    margin-bottom: 1.4rem;

    &:not(:first-child) {
        margin-top: 1.4rem;
        // border-top: 3px solid $color-primary;
    }

    &:first-child {
        border-top-left-radius: 3px;
        border-top-right-radius: 3px;
    }

    &__referer {
        text-decoration: none;
        color: $color-white;
        border-bottom: 1px solid transparent;
        transition: all .1s;
        display: flex;
        flex-direction: row;
        align-items: center;
        font-size: 1.4rem;
        
        p {
            margin-left: .5rem;
        }

         &:hover {
             border-bottom: 1px solid $color-white;
         }
    }

}



