.checkbox {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    height: 100%;
    font-size: 1.5rem;

    &__label {
        margin-right: .8rem;
        font-size: 1.4rem;
        i {
            margin-left: .5rem;
            color: $color-white;
            background-color: white;
            border: 1px solid $color-primary;
            padding: 3px;
            border-radius: 2px;
            font-size: 1.2rem;
            transition: all .2s;
            cursor: pointer;
        }
    }

    &__input {
        display: none;
    }

    &__input:checked + &__label i {
        background-color: $color-primary;
        color: $color-white;
    }
}