.form__dropmenu {
    background-color: $color-white;
    display: flex;
    align-items: center;
    flex-direction: row-reverse;
    border: none;
    padding-right: 1rem;
    margin-top: 1rem;
    margin-bottom: 0;
    cursor: pointer;
    @include boxshadow;
}

.dropmenu-container {
    position: relative;
}

.dropmenu__list-container {
    background-color: #efefef;
    overflow: hidden;
    transition: .4s max-height ease-out;
    height: auto;
    max-height: 50rem;
    padding: 0 1rem;
    border-bottom-left-radius: 5px;
    border-bottom-right-radius: 5px;


    .form__input {
        margin-bottom: 0rem;
    }
    
    &.open {
    }
    
    &.close {
        max-height: 0;
    }

}



.form__dropmenu + .form__input--icon {
    color: $color-primary;
    left: 1.1rem;
    top: .9rem;
    right: auto;
    cursor: pointer;

}