.not-found {
    background-color: red;
    text-align: center;

    &__top {
        display: flex;
        justify-content: space-around;
        align-content: center;
        flex-wrap: wrap;
        background-color: $color-primary;
        height: 50vh;
        position: relative;
        color: $color-bg-grey;
    }

    &__bottom {
        background-color: $color-bg-grey;
        height: 50vh;
        padding-top: 4rem;
        display: flex;
        flex-wrap: nowrap;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        align-content: center;
        flex-wrap: wrap;
        @include respond(fold-phone) {
            padding: 4rem 1rem;
        }

        img {
            margin: 0 auto;
            width: 35rem;
            margin-bottom: 5rem;

            @include respond(fold-phone) {
                width: 100%;
            }
        }
    }

    &__status-code {
        font-family: kdam, sans-serif;
        font-size: 20rem;
        width: 100%;

        @include respond(phone) {
            font-size: 15rem;
        }

        @include respond(fold-phone) {
            font-size: 15rem;
        }
    }

    &__message {
        margin-top: -5rem;
        font-size: 3rem;

        @include respond(phone) {
            font-size: 3rem;

        }
    }

    &__btn {
        width: 35rem;
        padding: 1.2rem 0rem;
        display: flex;
        justify-content: center;
        align-items: center;
        background-color: $color-primary;
        color: $color-white;
        text-decoration: none;
        font-size: 1.6rem;
        border-radius: 5px;
        transition: all .2s;
        box-shadow: 0 .5rem .5rem rgba(black, .2);
        backface-visibility: hidden;

        @include respond(fold-phone) {
            width: 100%;
        }

        &:hover {
            transform: translateY(-3px);
            box-shadow: 0 .8rem .8rem rgba(black, .2);
        }

        &:active {
            transform: translateY(-1px);
            box-shadow: 0 .3rem .3rem rgba(black, .2);
        }

        i {
            font-size: 1.8rem;
            margin-right: 1.5rem;
        }
    }


}