.pagination-container {

    //margin-top: 1rem !important;
    background-color: white;
    width: 100%;
    margin: 0 auto;
    list-style: none;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    height: 4.5rem;
    border-radius: 5px;
    border-top-right-radius: 0px;
    border-top-left-radius: 0px;
    padding: .5rem 0;
    @include boxshadow;

    .pagination {
        
        &__disabled {

        }
        
        &__active {
            background-color: $color-primary;
            color: white !important;
            height: 25px;
            width: 25px;
            display: grid;
            place-items: center;
            border-radius: 50%;
            transition: all .2s;

            @include respond(big-desktop) {
                height: 30px;
                width: 30px;
            }

            &--link {
                outline: none;    
            }
        }
        
        &__page {
            color: $color-text-grey;
            font-size: 1.4rem;
            transition: all .2s;
            display: grid;
            place-items: center;
            height: 25px;
            width: 25px;
            border-radius: 50%;

            @include respond(big-desktop) {
                height: 30px;
                width: 30px;
            }

            &:hover {
                background-color: #efefef;
            }

            &--link {
                cursor: pointer;
            }
        }
        
        &__previous {
            color: $color-primary;
            font-size: 1.4rem;
            
            &--link {
                outline: none;
                cursor: pointer;
            }
        }
    
        &__next {
            font-size: 1.4rem;
            color: $color-primary;

            &--link {
                outline: none;
                cursor: pointer;
            }
    
        }
    }
}

