.react-datepicker-wrapper {
    width: 100%;
}

.react-datepicker {
    font-size: 1rem !important;
    transform: scale(1.5) translate3d(0px , 0px, 0px);
    margin-top: 3rem;

    @include respond(small-phone) {
        transform: scale(1.5) translate3d(40px , 0px, 0px);
    }

    @include respond(big-desktop) {
        margin-top: .4rem;
        transform: scale(1.2);
    }
}


.form-group__1-col-datepicker {
    width: 100%;
}

.form-group__2-col-datepicker {
    width: 48%;

    @include respond(small-phone) {
        width: 100%;
    }
}

.form-group__3-col-datepicker {
    width: 31%;
    
    @include respond(small-phone) {
        width: 100%;
    }

}

.form-control-datepicker {
    i {
        position: absolute;
        top: -4.1rem;
    }
}

.icon-container {
    position: relative;
}