.list-container {
    display: flex;
    position: relative;

    @include respond(phone) {
        // to display table as readable for users
        overflow-y: scroll;
    }

}

.list {
    width: 100%;
    padding: 3rem 0;
    
    @include respond(tab-land) {
        width: 100%;
        padding: 5rem 0;
    }


    .btn {
        display: inline-block;
        padding: .3rem 1rem;
        color: $color-white;
        
        @include respond(fold-phone) {
            display: inline-block;
            width: 6.2rem;
        }
    }

    &-table-page-container {
        width: 80%;
        margin: 0 auto;
        
        @include respond(phone) {
            // to display table as readable for users
            min-width: 400px;
            padding: 0 1rem;
    
        }

        @include respond(tab-port) {
            width: 90%;
        }
    }

    &-table {
        width: 100%;
        border-radius: 3px;
        border-collapse: collapse;
        overflow: hidden;

        @include boxshadow;

        &__row-header {
            height: 40px;
        }
        
        &__col-header {
            padding: .7rem 1rem .7rem 0;
            font-size: 1.5rem;
            background-color: $color-primary;
            color: $color-white;
            font-weight: 100;

            &:not(:first-child) {
                border-left: 2px solid rgba(black, .05)
            }
        }

        &__row-item {
            height: 50px;
            color: $color-text-grey;

            &.highlight-error {
                color: $color-danger !important;
                background-color: lighten($color-danger, 45) !important;
            }

            &.highlight-warning {
                color: $color-warning !important;
                background-color: #FFF3CD !important;
            }
            
            &:not(:last-child) {
                border-bottom: 2px solid rgba(black, .05);
            }
            background-color: white;

            &:nth-child(even) {
                background-color: rgba(0,0,0,.05);;
            }
        }

        &__col-item {
            padding: .7rem 2rem .7rem 0;
            font-size: 1.5rem;
            font-weight: 100;

            @include respond(tab-port) {
                font-size: 1.6rem;
            }

            @include respond(phone) {
                font-size: 1.7rem;
                padding: .7rem .7rem .7rem 0;
            }

            &:not(:first-child) {
                border-left: 2px solid rgba(black, .05);
            }


            &--btn {
                width: 12rem;
                text-align: center;

                @include respond(small-phone) {
                    width: auto;
                }
            }
        }
    }
}


.no-padding-top .list {
    padding-top: 0;
}