.qr-sticker-constainer {
    display: flex;
    justify-content: space-between;
    position: relative;

    @include respond(phone) {
        flex-direction: column;
    }
}

.qr-sticker {
    width: 49%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;

    @include respond(phone) {
        width: 100%;
        margin-top: 3rem;
        margin-bottom: 3rem;
    }
}

.form-group-qr {
    width: 49%;

    @include respond(phone) {
        width: 100%;
    }
}

.display-on-print {
    // make it none
    display: none;
}

.right-side-logo {
    position: absolute;
    top: 35%;
    right: -50px;
    transform: rotate(90deg) translateY(-150%);
    border-bottom: 2px solid $color-primary;
    padding-bottom: 1rem;
    width: 312px;
    text-align: center;
    img {
        width: 150px;
    }
}

.print-btn {
    background-color: $color-primary;
    padding: .6rem 1.5rem;
    border-radius: 3px;
    @include boxshadow;
    color: $color-white;
    font-size: 1.4rem;
    text-align: center;
    margin-top: 1rem;
    cursor: pointer;
    transition: all .2s;
    backface-visibility: hidden;

    &:hover {
        transform: translateY(-3px);
        box-shadow: 0 .5rem .8rem rgba(black, .2);
    }

    &:active,
    &:focus {
        transform: translateY(-1px);
        box-shadow: 0 .1rem .3rem rgba(black, .2);
    }

    i {
        margin-left: .5rem;
    }

    @include respond(phone) {
        margin: 1rem 0;
    }
}
